@use 'base/variables' as v;
@use 'base/mixins' as m;

.login { // para aplicar degradado a las letras de UpTask
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$cyan);
    }   

    input[type="submit"] {
        @include m.boton( v.$cyan , v.$blanco);
    }

    .acciones {
        a {
            color: v.$cyan;
        }
    }
}