@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    background-color: v.$gris_claro;

    @include m.tablet {
        display: flex;
        height: 100vh; // Viewport Height
    }

    .principal {
        flex: 1; // para que tome todo el espacio de la barra superior        

        @include m.tablet {
            overflow: auto;  // con esto se evita que se corte el color de fondo del dashboard el bajar para ver todas las tareas
        }
    }

    .contenido {
        padding: 2rem;

        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;
        }

    }

    .formulario {
        input:not([type="submit"]) {
            background-color: v.$blanco;
        }

        input[type="submit"] {
            @include m.boton(v.$indigo, v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.4rem;
        }
    }
    .enlace {
        color: v.$indigo;
        &:hover {
            color: darken(v.$indigo, 20%);
        }
    }
}