@use 'base/variables' as v;
@use 'base/mixins' as m;

@media ( prefers-color-scheme: dark ) {
    $grisOscuro: #2c2c3b;
    body {
        background-color: $grisOscuro; 
    }

    .tagline,
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a {
        color: v.$blanco;
    }

    .descripcion-pagina, 
    .formulario .campo input,
    .nombre-pagina {
        color: darken(v.$gris_claro, 15%);
    }

    .formulario .campo input {
        background-color: lighten( $grisOscuro, 15% );
        color: v.$blanco;
        &::placeholder { // a los placeholder se le da estilo con ampersand dos puntos dos puntos &::
            color: darken(v.$gris_claro, 30%);
        }
    }

    .alerta.error {
        background-color: v.$blanco;
    }

    .sidebar {

        @include m.tablet {       
            background-color: darken( $grisOscuro, 5% );
        }
        
        .sidebar-nav a {
            &.activo,
            &:hover {
                background-color: v.$naranja;
            }
        }

    }

    .barra-mobile,
    .barra {
        background-color:  darken( $grisOscuro, 7% );
    }

    .barra {

        p {
            color: v.$naranja;
            span {
                color: v.$gris_claro; 
            }
        }        
        .cerrar-sesion { 
            @include m.boton( v.$naranja, v.$blanco );
            padding: 1rem;
            font-size: 1.2rem;
        }
        
    }

    .filtros {
        background-color: lighten( $grisOscuro, 10% );
        h2,
        label {
            color: v.$blanco;
        }
        input[type="radio"] {
            accent-color: v.$naranja;
        }
    }

    .dashboard {
        background-color: $grisOscuro;

        .enlace {
            color: v.$gris_claro;

            &:hover {
                color: darken( v.$gris_claro, 20%);
            }
        }

        .formulario input:not([type=submit]) {
            background-color: lighten($grisOscuro, 20%);
            color: v.$blanco;
            &::placeholder { // a los placeholder se le da estilo con ampersand dos puntos dos puntos &::
                color: darken(v.$gris_claro, 30%);
            }
        }
    }

    .listado-proyectos .proyecto {
        background-color: lighten($grisOscuro, 20%);
        // AND hover
        &:hover {
            background-color: lighten($grisOscuro, 15%); // como se esta aclando con lighten al bajar el porcentaje oscurece
        }
    }

    .listado-tareas .tarea {
         background-color: lighten($grisOscuro, 10%);
         border: none;
         p {
            color: v.$gris_claro;
         }
    }

    .modal .formulario {
        background-color: lighten($grisOscuro, 10%);
        input[type="text"] {
            background-color: lighten($grisOscuro, 20%);
            border: none;
            color: v.$blanco;
            &::placeholder { // a los placeholder se le da estilo con ampersand dos puntos dos puntos &::
                color: darken(v.$gris_claro, 20%);
            }
        }
        legend {
            color: v.$gris_claro;
        }       
    }
}