@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones {
    margin-top: 5rem;
    @include m.tablet {
        display: flex; // coloca los enlaces en una misma linea horizontal
        justify-content: space-between; // separa horizontalmente los enlaces
    }

    a {
        display: block; // para que cada enlace tome su propia línea
        text-align: center;
        margin-bottom: 2rem; // para separar cada enlace
        color: v.$negro;
        &:last-of-type {
            margin-bottom: 0; // para quitar el margen inferior anaranjado del ultimo enlace (olvido)
        }
    }
}